// External Module: React
import React from 'react';

// External Module: MUI Material
import ListItemButton from '@mui/material/ListItemButton';

// How an individual song is rendered
export default function Song({
    name,
    duration,
    id,
    onClick,
    selected,
    disabled,
}) {
    return (
        <ListItemButton
            key={`song_${id}`}
            onClick={onClick}
            disableRipple={true}
            disabled={disabled}
            selected={selected}
            className='song'>
            <div className='flex-row container'>
                <span className='name'>{name}</span>
                <span className='duration'>{duration}</span>
            </div>
        </ListItemButton>
    );
}
