// External Module: React
import React from 'react';

// External Module: MUI Material
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';

// External Module: MUI Icons
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';
import KeyboardDoubleArrowLeftIcon from '@mui/icons-material/KeyboardDoubleArrowLeft';
import KeyboardDoubleArrowRightIcon from '@mui/icons-material/KeyboardDoubleArrowRight';
import KeyboardDoubleArrowUpIcon from '@mui/icons-material/KeyboardDoubleArrowUp';

export default function ButtonControls({
    selectedSong,
    setSongOrder,
    songOrder,
}) {
    // Whether the button is disabled
    const disabled = !selectedSong;

    // Location of song on the album
    const albumSide =
        selectedSong && songOrder.a.includes(selectedSong) ? 'a' : 'b';
    const otherSide = selectedSong && albumSide === 'a' ? 'b' : 'a';
    const isOnASide = selectedSong && songOrder.a.includes(selectedSong);
    const isOnBSide = selectedSong && songOrder.b.includes(selectedSong);

    // Location of song on the side
    const isFirst =
        selectedSong &&
        ((songOrder.a.length > 0 && songOrder.a[0] === selectedSong) ||
            (songOrder.b.length > 0 && songOrder.b[0] === selectedSong));
    const isLast =
        selectedSong &&
        ((songOrder.a.length > 0 &&
            songOrder.a[songOrder.a.length - 1] === selectedSong) ||
            (songOrder.b.length > 0 &&
                songOrder.b[songOrder.b.length - 1] === selectedSong));

    // Button Actions
    const toOtherSide = () => {
        // Construct the side that omits the selected song
        const omittedOrder = songOrder[albumSide].filter(
            id => id !== selectedSong
        );
        const addedOrder = [selectedSong, ...songOrder[otherSide]];

        // Invoke our order change
        setSongOrder({ [albumSide]: omittedOrder, [otherSide]: addedOrder });
    };
    const higher = () => {
        // Select the desired set of IDs
        const ids = songOrder[albumSide];

        // Get the index that the song currently resides at
        const index = ids.indexOf(selectedSong);

        // Construct the new song order
        const newIds = [
            ...ids.slice(0, index - 1),
            selectedSong,
            ids[index - 1],
            ...ids.slice(index + 1),
        ];

        setSongOrder({
            [albumSide]: newIds,
            [otherSide]: songOrder[otherSide],
        });
    };
    const lower = () => {
        // Select the desired set of IDs
        const ids = songOrder[albumSide];

        // Get the index that the song currently resides at
        const index = ids.indexOf(selectedSong);

        // Construct the new song order
        const newIds = [
            ...ids.slice(0, index),
            ids[index + 1],
            selectedSong,
            ...ids.slice(index + 2),
        ];

        setSongOrder({
            [albumSide]: newIds,
            [otherSide]: songOrder[otherSide],
        });
    };
    const toFirst = () => {
        // Select the desired set of IDs
        const ids = songOrder[albumSide];

        // Get the index that the song currently resides at
        const index = ids.indexOf(selectedSong);

        // Construct the new song order
        const newIds = [
            selectedSong,
            ...ids.slice(0, index),
            ...ids.slice(index + 1),
        ];

        setSongOrder({
            [albumSide]: newIds,
            [otherSide]: songOrder[otherSide],
        });
    };
    const toLast = () => {
        // Select the desired set of IDs
        const ids = songOrder[albumSide];

        // Get the index that the song currently resides at
        const index = ids.indexOf(selectedSong);

        // Construct the new song order
        const newIds = [
            ...ids.slice(0, index),
            ...ids.slice(index + 1),
            selectedSong,
        ];

        setSongOrder({
            [albumSide]: newIds,
            [otherSide]: songOrder[otherSide],
        });
    };

    return (
        <Grid
            container
            flexDirection={'column'}
            alignContent={'center'}
            justifyContent={'center'}
            gap={1}
            sx={{ pl: 2, pr: 2 }}>
            <Button
                variant='contained'
                disabled={disabled || isOnBSide}
                onClick={toOtherSide}>
                <KeyboardDoubleArrowRightIcon />
            </Button>
            <Button
                variant='contained'
                disabled={disabled || isFirst}
                onClick={toFirst}>
                <KeyboardDoubleArrowUpIcon />
            </Button>
            <Button
                variant='contained'
                disabled={disabled || isFirst}
                onClick={higher}>
                <KeyboardArrowUpIcon />
            </Button>
            <Button
                variant='contained'
                disabled={disabled || isLast}
                onClick={lower}>
                <KeyboardArrowDownIcon />
            </Button>
            <Button
                variant='contained'
                disabled={disabled || isLast}
                onClick={toLast}>
                <KeyboardDoubleArrowDownIcon />
            </Button>
            <Button
                variant='contained'
                disabled={disabled || isOnASide}
                onClick={toOtherSide}>
                <KeyboardDoubleArrowLeftIcon />
            </Button>
        </Grid>
    );
}
