// External Module: React
import React from 'react';

// External Module: MUI Material
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';

// Internal Modules
import Duration from './Duration';

// Constants
const MIN_SIDE_DURATION = new Duration('5:00');
const MAX_SIDE_DURATION = new Duration('19:30');

export default function AlbumSide({ side, duration, children }) {
    // Do we have an error?
    let errMsg;
    if (duration.asSeconds > MAX_SIDE_DURATION.asSeconds) {
        errMsg = `Side is too long. Limit is ${MAX_SIDE_DURATION.asString}.`;
    } else if (duration.asSeconds < MIN_SIDE_DURATION.asSeconds) {
        errMsg = `Side is too short. Minimum is ${MIN_SIDE_DURATION.asString}.`;
    }

    return (
        <List sx={{ pt: 0 }}>
            <ListItem
                key={`album-side-${side}`}
                className={`album-side ${errMsg ? 'in-error' : ''}`}>
                <div className='container'>
                    <div className='flex-row'>
                        <span>Side {side}</span>
                        <span className='duration'>{duration.asString}</span>
                    </div>
                    {errMsg && <div className='error-message'>{errMsg}</div>}
                </div>
            </ListItem>
            {children}
        </List>
    );
}
