// Class to manage durations (as strings, integers)
export default class Duration {
    constructor(value) {
        // Is the value a string?
        if (typeof value === 'string') {
            // Parse as a regular expression
            const regexp = /([0-9]{1,2}):([0-9]{2})/;
            const match = value.match(regexp);
            if (match) {
                const minutes = parseInt(match[1]);
                const seconds = parseInt(match[2]);
                this.total = minutes * 60 + seconds;
            }
        } else if (typeof value === 'number') {
            // Assumed to be total seconds
            this.total = value;
        }
    }
    get asSeconds() {
        return this.total;
    }
    get asString() {
        // Convert the duration into minutes and seconds
        const minutes = Math.trunc(this.total / 60);
        const seconds = this.total - minutes * 60;

        return `${minutes}:${(seconds + '').padStart(2, '0')}`;
    }
    add(other) {
        // Verify that other is a Duration
        return new Duration((this.total += other.total));
    }
}
